import axios from "axios";
import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";

const KakaoLogin = () => {
  //     
  const KAKAO_AUTH_URI = `https://kauth.kakao.com/oauth/authorize?client_id=${process.env.REACT_APP_REST_API_KEY}&redirect_uri=${process.env.REACT_APP_REDIRECT_URL}&response_type=code`;
  //  
  const code = new URL(window.location.href).searchParams.get("code");

  //   MyPage  useNavigate 
  const navigate = useNavigate();
  useEffect(() => {
    //////////////////////////////////////////////////////////////
    //          jwt  
    ////////////////////////////////////////////////////////////
    (async () => {
      try {
        const res = await axios
          //      GET 
          //          .
          .get(
            `http://back.fuseable.monster:8080/api/oauth/token?code=${code}`
          )
          //     
          .then((response) => {
            // console.log(" ", response);
            // ,
            //     authorization   token   
            const token = response.headers.authorization;
            //   ,     .
            sessionStorage.setItem("token", token);
            // console.log("Stored token",token);
          });
      } catch (e) {
        //   ,   
        console.error(e);
      }

      //  setItem       .
      // ,      token   .
      const token = sessionStorage.getItem("token");
      // console.log("Storage token",token);
      ////////////////////////////////////////////////////////////
      //                  
      ////////////////////////////////////////////////////////////
      try {
        const res = await axios
          // , post  get .
          //      .
          .get(
            "http://back.fuseable.monster:8080/api/me",
            {
              //    Authorization request   /
              headers: {
                Authorization: token,
                request: token,
              },
            }
          )
          //        ,   .
          // data     , setItem    .
          .then((data) => {
            sessionStorage.setItem("Email", JSON.stringify(data.data.accountEmail));
            sessionStorage.setItem("Id", JSON.stringify(data.data.accountId));
            sessionStorage.setItem("Nickname", JSON.stringify(data.data.accountNickname));
            sessionStorage.setItem("ProfileImg", JSON.stringify(data.data.profileImg));
            sessionStorage.setItem("userCode", JSON.stringify(data.data.userCode));
            sessionStorage.setItem("userRole", JSON.stringify(data.data.userRole));

            console.log("DATA", data);

            
            // ,    navigate       .
            if (data) {
              navigate("/start");
            }
          });
      } catch (e) {
        //   ,   
        console.error(e);
      }
    })();
  }, []);

  return (
    <>
      <div className="KaKaoBtn">
        <a href={KAKAO_AUTH_URI}> </a>
      </div>
    </>
  );
};


export default KakaoLogin;